import React, { useRef, useState } from 'react';

const VoiceBox2 = () => {
  const recognitionRef = useRef(null); //   SpeechRecognition
  const [transcript, setTranscript] = useState(''); //   
  const [isRecording, setIsRecording] = useState(false); //  

  //   SpeechRecognition
  const initializeRecognition = () => {
    const SpeechRecognition = window.SpeechRecognition || window.webkitSpeechRecognition;

    if (!SpeechRecognition) {
      alert('Web Speech API     .');
      return null;
    }

    const recognition = new SpeechRecognition();
    recognition.lang = 'fa-IR'; //  
    recognition.interimResults = true; //   
    recognition.onresult = (event) => {
      const interimTranscript = Array.from(event.results)
        .map((result) => result[0].transcript)
        .join('');
      setTranscript(interimTranscript);
    };

    recognition.onerror = (event) => {
      console.error('  :', event.error);
    };

    return recognition;
  };

  //  
  const startRecognition = () => {
    if (!recognitionRef.current) {
      recognitionRef.current = initializeRecognition();
    }
    if (recognitionRef.current) {
      recognitionRef.current.start();
      setIsRecording(true);
    }
  };

  //  
  const stopRecognition = () => {
    if (recognitionRef.current) {
      recognitionRef.current.stop();
      setIsRecording(false);
    }
  };

  return (
    <div style={{ textAlign: 'center', marginTop: '50px' }}>
      <h1> : {isRecording ? '  ...' : ''}</h1>
      <button
        type="button"
        style={{
          padding: '20px',
          fontSize: '18px',
          backgroundColor: isRecording ? 'red' : 'green',
          color: 'white',
          border: 'none',
          borderRadius: '10px',
        }}
        onMouseDown={startRecognition} //      
        onMouseUp={stopRecognition} //      
        onTouchStart={startRecognition} //  
        onTouchEnd={stopRecognition} //  
      >
        {isRecording ? ' ...' : '   '}
      </button>
      <p style={{ marginTop: '30px', fontSize: '18px' }}>{transcript}</p>
    </div>
  );
};

export default VoiceBox2;
